var that;
export default {
	data() {
		return {
			loading:false,
			tabCar: '',
			sort_type: '',
			color: 'bg-kong', 
			topheight: 0,
			goods_list: [],
			mch_id:'',
			shop:'',
			page:1,
			nolist:false,
			nomore:false,
			keyword:''
		};
	},
	methods: {
		getsearch(e){
			that.keyword=e.detail.value;
		},
		search(e){
			that.page = 1;
			that.goods_list=[],
			that.getmchinfo();
		},
		qie(e) {
			let index = e.currentTarget.dataset.index;
			that.sort = index;
			if (index == 3) {
				if (that.sort_type != -1) {
					that.sort_type == 1 ? (that.sort_type =2) : (that.sort_type = 1);
				} else {
					that.sort_type = 1;
				}
			} else {
				that.sort_type = '';
			}
			that.tabCar=index
			that.page = 1;
			that.goods_list=[],
			that.getmchinfo();
		},
		gund(e) {
			let top = e.detail.scrollTop;
			if (top > that.topheight) {
				that.color = 'bg-white';
			} else {
				that.color = 'bg-kong';
			}
		},
		getmchinfo() {
			that.loading=true;
			this.$request
				.request({
					url: that.api.mch.shop,
					data: {
						mch_id: that.mch_id,
						tab: 2,
						sort: that.sort_type==2?4:that.tabCar,
						page: that.page, 
						keyword:that.keyword
					}
				})
				.then(res => {
					console.log(res)
					that.shop=res.shop;
					let goods_list=res.goods_list; 
					if (goods_list.length > 0) {
						that.goods_list = that.goods_list.concat(goods_list);
						that.page++; 
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了！');
								that.nomore = true;
							}
						} else {
							that.nolist = true;
						}
					}
					uni.stopPullDownRefresh()
					that.loading=false;
				});
		}
	},
	onLoad(option) {
		that = this;
		that.mch_id=option.mch_id?option.mch_id:''; 
		that.getmchinfo();
	},

	onReady() {
		const query = uni.createSelectorQuery();
		query
			.select('.top-view')
			.boundingClientRect(function(res) {
				that.topheight = res.height;
			})
			.exec();
	},
	onReachBottom() {
		that.getmchinfo();
	}
};
